// Imports
import ___CSS_LOADER_API_SOURCEMAP_IMPORT___ from "../../../../node_modules/css-loader/dist/runtime/cssWithMappingToString.js";
import ___CSS_LOADER_API_IMPORT___ from "../../../../node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, "#content[data-v-78097d02]{margin-top:10%}", "",{"version":3,"sources":["webpack://./resources/js/pages/user/Dashboard.vue"],"names":[],"mappings":"AAkKA,0BACA,cACA","sourcesContent":["<template>\r\n  <div id=\"main\">\r\n    <div id=\"header\">\r\n      <HeaderComponent />\r\n    </div>\r\n    <div class=\"container\" id=\"content\">\r\n      <div v-if=\"this.loading\" class=\"d-flex align-items-center container\">\r\n        <strong>Loading...</strong>\r\n        <div\r\n          class=\"spinner-border ms-auto\"\r\n          role=\"status\"\r\n          aria-hidden=\"true\"\r\n        ></div>\r\n      </div>\r\n      <div v-else>\r\n        <div>\r\n          <h3>แดชบอร์ด</h3>\r\n          <p>\r\n            <u>วันนี้: {{ currentDateTime() }}</u>\r\n          </p>\r\n          <hr />\r\n          <div class=\"card p-3 mb-3 bg-light\">\r\n            <div class=\"card-body\">\r\n              <span\r\n                ><p>สวัสดี!, {{ user_name }}</p></span\r\n              >\r\n              <p>คุณพร้อมที่จะเริ่มเรียนรู้แล้วหรือยัง...?</p>\r\n            </div>\r\n          </div>\r\n        </div>\r\n        <div v-show=\"this.DataProgress != ''\">\r\n          <h3>การ์ดความคืบหน้า</h3>\r\n          <div class=\"row\">\r\n            <div\r\n              v-for=\"data in DataProgress\"\r\n              :key=\"data.id\"\r\n              class=\"col-md-4 my-3\"\r\n              type=\"button\"\r\n              @click=\"clickLearning(data.id)\"\r\n            >\r\n              <div class=\"card-deck\">\r\n                <div class=\"card shadow p-1 mb-5 bg-white rounded\">\r\n                  <div class=\"card-body\">\r\n                    <div class=\"card-text\">\r\n                      <h6>วิชา: {{ data.subjectName }}</h6>\r\n                      <p class=\"badge badge-warning\">\r\n                        หมวดหมู่: {{ data.subjectType }}\r\n                      </p>\r\n                      <p><u>ความคืบหน้า</u></p>\r\n                      <div class=\"progress\">\r\n                        <div\r\n                          class=\"\r\n                            progress-bar\r\n                            progress-bar-striped\r\n                            progress-bar-animated\r\n                          \"\r\n                          role=\"progressbar\"\r\n                          :style=\"{ width: data.count_progress + '%' }\"\r\n                          aria-valuenow=\"15\"\r\n                          aria-valuemin=\"0\"\r\n                          aria-valuemax=\"100\"\r\n                        >\r\n                          {{ data.count_progress }}%\r\n                        </div>\r\n                      </div>\r\n                    </div>\r\n                  </div>\r\n                </div>\r\n              </div>\r\n            </div>\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </div>\r\n\r\n    <div class=\"footer\" id=\"footer\">\r\n      <FooterComponent />\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script scoped>\r\nimport axios from \"axios\";\r\nimport HeaderComponent from \"../../components/HeaderComponent.vue\";\r\nimport FooterComponent from \"../../components/FooterComponent.vue\";\r\nexport default {\r\n  name: \"dashboard\",\r\n  components: { HeaderComponent, FooterComponent },\r\n  data() {\r\n    return {\r\n      loading: true,\r\n      DataProgress: [],\r\n      user_name: [],\r\n    };\r\n  },\r\n  mounted() {\r\n    this.DataProgressSubject();\r\n    if (this.$store.state.token !== \"\") {\r\n      axios\r\n        .get(\"/api/auth/user-profile/\", {\r\n          headers: {\r\n            \"Content-Type\": \"application/json\",\r\n            Authorization: \"Bearer \" + this.$store.state.token,\r\n          },\r\n        })\r\n        .then((res) => {\r\n          //   alert(\"Axios Dashboard Success!!\");\r\n          this.user_name = res.data[0].name;\r\n          this.loading = false;\r\n        })\r\n        .catch((err) => {\r\n          //   alert(\"error axios Dashboard!!\");\r\n          this.$store.commit(\"clearToken\");\r\n          this.$router.push(\"/learning/login\");\r\n        });\r\n    } else {\r\n      //   alert(\"error this.$store.state.token!!\");\r\n      this.loading = false;\r\n      this.$router.push(\"/learning/login\");\r\n    }\r\n  },\r\n  methods: {\r\n    DataProgressSubject() {\r\n      axios\r\n        .get(\"/api/my_subject_leasson_left/\" + this.$store.state.user_id)\r\n        .then((res) => {\r\n          //   alert(\"Axios Dashboard Success!!\");\r\n          this.DataProgress = res.data;\r\n          //   this.loading = false;\r\n          //   console.log(this.DataProgress);\r\n        })\r\n        .catch((err) => {\r\n          //   alert(\"error axios Dashboard!!\");\r\n          this.$store.commit(\"clearToken\");\r\n          this.$router.push(\"/learning/login\");\r\n        });\r\n    },\r\n    clickLearning(id) {\r\n      this.$router.push({\r\n        name: \"preface2\",\r\n        params: { id: id },\r\n      });\r\n    },\r\n    currentDateTime() {\r\n      const current = new Date();\r\n      const date =\r\n        (current.getDate() < 10 ? \"0\" : \"\") +\r\n        current.getDate() +\r\n        \"-\" +\r\n        (current.getMonth() < 10 ? \"0\" : \"\") +\r\n        (current.getMonth() + 1) +\r\n        \"-\" +\r\n        (current.getFullYear() + 543);\r\n      const dateTime = date;\r\n\r\n      return dateTime;\r\n    },\r\n  },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n#content {\r\n  margin-top: 10%;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
