":[],"mappings":"AAyNA,8BAEA,kDAAA,CACA,wBAAA,CAFA,4BAAA,CAGA,cAAA,CAEA,UAAA,CADA,YAEA,CACA,4CACA,sBAAA,CAMA,WAAA,CAFA,eAAA,CACA,eAAA,CAFA,UAAA,CAMA,eAAA,CAPA,qBAAA,CADA,kBAMA,CAIA,kDACA,aACA,CACA,kDACA,UACA,CACA,2CAEA,aAAA,CADA,eAEA,CAEA,2DACA,aACA,CACA,mEAGA,eAAA,CAEA,WAAA,CAAA,mCAAA,CACA,eAAA,CAEA,eAAA,CADA,UAAA,CAEA,aAAA,CARA,oBAAA,CASA,kCAAA,CARA,SASA,CACA,yEACA,4BACA,CAEA,4DACA,UACA,CAEA,gCAIA,UAAA,CACA,cAAA,CAJA,mBAAA,CAEA,eAAA,CADA,WAAA,CAIA,yBAAA,CACA,wBAAA,CAAA,qBAAA,CAAA,oBAAA,CAAA,gBACA,CACA,sCAEA,+CAAA,CADA,UAEA,CACA,8DACA,aAAA,CACA,eACA,CAIA,yCACA,8BAEA,kDAAA,CACA,wBAAA,CAFA,4BAAA,CAGA,cAAA,CAEA,UAAA,CADA,YAEA,CACA,iCACA,WAAA,CAGA,kBAAA,CAFA,iBAAA,CACA,eAEA,CACA,yCAKA,oBAAA,CAHA,QAAA,CACA,SAAA,CACA,UAIA,CACA","sourcesContent":["<template>\r\n  <div>\r\n    <nav class=\"main-navbar navbar-expand-lg navbar-light bg-light sticky-top\">\r\n      <router-link class=\"navbar-brand active\" to=\"/\" style=\"font-size: 25px\"\r\n        >E-Learning</router-link\r\n      >\r\n      <button\r\n        class=\"navbar-toggler\"\r\n        type=\"button\"\r\n        data-toggle=\"collapse\"\r\n        data-target=\"#navbarSupportedContent\"\r\n        aria-controls=\"navbarSupportedContent\"\r\n        aria-expanded=\"false\"\r\n        aria-label=\"Toggle navigation\"\r\n      >\r\n        <span class=\"navbar-toggler-icon\"></span>\r\n      </button>\r\n      <div class=\"collapse navbar-collapse\" id=\"navbarSupportedContent\">\r\n        <ul class=\"navbar-nav mr-auto\">\r\n          <li class=\"nav-item\">\r\n            <router-link class=\"nav-link\" to=\"/\"\r\n              >Home <span class=\"sr-only\">(current)</span></router-link\r\n            >\r\n          </li>\r\n          <li class=\"nav-item\">\r\n            <router-link class=\"nav-link\" to=\"/learning/course\">Course</router-link>\r\n          </li>\r\n          <li class=\"nav-item\">\r\n            <router-link class=\"nav-link\" to=\"/learning/news\">News</router-link>\r\n          </li>\r\n          <li class=\"nav-item\">\r\n            <router-link class=\"nav-link\" to=\"/learning/teacher\">Teacher</router-link>\r\n          </li>\r\n          <li class=\"nav-item\">\r\n            <router-link class=\"nav-link\" to=\"/learning/contact\">Contact</router-link>\r\n          </li>\r\n        </ul>\r\n        <div>\r\n          <div v-if=\"this.$store.state.token !== ''\">\r\n            <span class=\"navbar-text\">\r\n              <div class=\"row d-flex align-items-center\">\r\n                <div class=\"col-md-0\">\r\n                  <router-link to=\"/learning/dashboard\"\r\n                    ><img\r\n                      class=\"rounded-circle\"\r\n                      :src=\"DataUser.user_photo\"\r\n                      width=\"35px\"\r\n                      height=\"35px\"\r\n                      alt=\"\"\r\n                  /></router-link>\r\n                </div>\r\n                <div class=\"col-md\">\r\n                  <router-link\r\n                    type=\"button\"\r\n                    to=\"/learning/dashboard\"\r\n                    style=\"text-decoration: none\"\r\n                    ><h5>{{ DataUser.name }}</h5></router-link\r\n                  >\r\n                </div>\r\n              </div>\r\n            </span>\r\n            <span class=\"navbar-text\">\r\n              <!-- Example split danger button -->\r\n              <div class=\"btn-group\">\r\n                <button\r\n                  type=\"button\"\r\n                  class=\"btn btn-danger dropdown-toggle dropdown-toggle-split\"\r\n                  data-bs-toggle=\"dropdown\"\r\n                  aria-expanded=\"false\"\r\n                ></button>\r\n                <ul class=\"dropdown-menu\">\r\n                  <li>\r\n                    <router-link class=\"dropdown-item\" to=\"/learning/dashboard\"\r\n                      >แดชบอร์ด</router-link\r\n                    >\r\n                  </li>\r\n                  <li>\r\n                    <router-link class=\"dropdown-item\" to=\"/learning/dashboard/lessons\"\r\n                      >บทเรียน</router-link\r\n                    >\r\n                  </li>\r\n                  <li>\r\n                    <router-link\r\n                      class=\"dropdown-item\"\r\n                      to=\"/learning/chat\"\r\n                      >แชทห้องเรียน</router-link\r\n                    >\r\n                  </li>\r\n                  <li>\r\n                    <router-link\r\n                      class=\"dropdown-item\"\r\n                      to=\"/learning/dashboard/timetables\"\r\n                      >ตารางเรียน</router-link\r\n                    >\r\n                  </li>\r\n                  <li>\r\n                    <router-link\r\n                      class=\"dropdown-item\"\r\n                      to=\"/learning/calendar_simulations\"\r\n                      >ลงทะเบียนเรียน</router-link\r\n                    >\r\n                  </li>\r\n                  <li>\r\n                    <router-link class=\"dropdown-item\" to=\"/learning/dashboard/profiles\"\r\n                      >แก้ไขโปรไฟล์</router-link\r\n                    >\r\n                  </li>\r\n                  <!-- <li>\r\n                  <a class=\"dropdown-item\" href=\"/learning/dashboard/certificates\">ใบประกาศนียบัตร</a>\r\n                </li> -->\r\n                  <!-- <li>\r\n                  <a class=\"dropdown-item\" href=\"/learning/dashboard/settings\">ตั้งค่า</a>\r\n                </li> -->\r\n                  <li><hr class=\"dropdown-divider\" /></li>\r\n                  <li>\r\n                    <a class=\"dropdown-item\"\r\n                      ><i class=\"fa fa-sign-out\" @click=\"logout\" id=\"log_out\">\r\n                        Sign out</i\r\n                      ></a\r\n                    >\r\n                  </li>\r\n                </ul>\r\n              </div>\r\n            </span>\r\n          </div>\r\n          <div v-else>\r\n            <router-link :to=\"{ name: 'login' }\"\r\n              ><span class=\"navbar-text\">\r\n                <a\r\n                  class=\"login btn action-button\"\r\n                  href=\"#\"\r\n                  style=\"text-decoration: none\"\r\n                  ><i class=\"fas fa-user\"></i> Log In</a\r\n                ></span\r\n              ></router-link\r\n            >\r\n            <router-link :to=\"{ name: 'register' }\">\r\n              <span class=\"navbar-text\">\r\n                <a\r\n                  class=\"btn btn-light action-button\"\r\n                  role=\"button\"\r\n                  href=\"#\"\r\n                  style=\"border-radius: 50px\"\r\n                  >Signup</a\r\n                >\r\n              </span>\r\n            </router-link>\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </nav>\r\n    <div style=\"margin-bottom: 66px\"></div>\r\n    <router-view></router-view>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  name: \"app\",\r\n  data() {\r\n    return {\r\n      DataUser: {},\r\n    };\r\n  },\r\n  mounted() {\r\n    if (this.$store.state.token !== \"\") {\r\n      axios\r\n        .get(\"/api/auth/user-profile/\", {\r\n          headers: {\r\n            \"Content-Type\": \"application/json\",\r\n            Authorization: \"Bearer \" + this.$store.state.token,\r\n          },\r\n        })\r\n        .then((res) => {\r\n          //   alert(\"Axios Dashboard Success!!\");\r\n          //   this.DataUser = res.data;\r\n          for (var i = 0; i < res.data.length; i++) {\r\n            this.DataUser = {\r\n              user_photo: res.data[i].user_photo,\r\n              name: res.data[i].name,\r\n            };\r\n          }\r\n          this.loading = false;\r\n          //   console.log(this.DataUser);\r\n        })\r\n        .catch((err) => {\r\n          //   alert(\"error axios Dashboard!!\");\r\n          this.$store.commit(\"clearToken\");\r\n          this.$router.push(\"/learning/login\");\r\n        });\r\n    } else {\r\n      // alert(\"error this.$store.state.token!!\");\r\n      this.loading = false;\r\n    }\r\n  },\r\n  methods: {\r\n    logout() {\r\n      axios\r\n        .post(\"/api/auth/logout\", { token: this.$store.state.token })\r\n        .then((res) => {\r\n          this.$store.commit(\"clearToken\");\r\n          //   this.$store.commit(\"clearCourse_id\");\r\n          this.$store.commit(\"clearUser_id\");\r\n          this.$router.push(\"/learning/login\");\r\n        })\r\n        .catch((err) => {\r\n          this.error = \"Error!!\";\r\n        });\r\n    },\r\n  },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n/* ##### >> https://fonts.google.com/specimen/Arimo */\r\n@import url(\"https://fonts.googleapis.com/css2?family=Arimo:wght@500&display=swap\");\r\n\r\n.main-navbar {\r\n  font-family: \"Arimo\", sans-serif;\r\n  background: linear-gradient(135deg, #fe2929, #ffb3c5);\r\n  background-color: #184e8e;\r\n  position: fixed;\r\n  z-index: 1020;\r\n  width: 100%;\r\n}\r\n.main-navbar .navbar-brand {\r\n  background: transparent;\r\n  padding-top: 0.75rem;\r\n  padding-bottom: 0.75rem;\r\n  color: #fff;\r\n  border-radius: 0;\r\n  box-shadow: none;\r\n  border: none;\r\n}\r\n.main-navbar .navbar-brand {\r\n  font-weight: bold;\r\n}\r\n.main-navbar .navbar-brand:hover {\r\n  color: #f0f0f0;\r\n}\r\n.main-navbar .navbar-brand:focus {\r\n  color: #fff;\r\n}\r\n.main-navbar .login:hover {\r\n  font-weight: bold;\r\n  color: #d9d9d9;\r\n}\r\n/* ########## Form Search ########## */\r\n.main-navbar .collapse .form-inline label {\r\n  color: #d9d9d9;\r\n}\r\n.main-navbar .collapse .form-inline .search-field {\r\n  display: inline-block;\r\n  width: 80%;\r\n  background: none;\r\n  border: none;\r\n  border-bottom: 1px solid transparent;\r\n  border-radius: 0;\r\n  color: #fff;\r\n  box-shadow: none;\r\n  color: inherit;\r\n  transition: border-bottom-color 0.3s;\r\n}\r\n.main-navbar .collapse .form-inline .search-field:focus {\r\n  border-bottom: 1px solid #ccc;\r\n}\r\n/* ########## color li ########## */\r\n.main-navbar .collapse .nav-item .nav-link {\r\n  color: #fff;\r\n}\r\n/* ########## mouse hover li ########## */\r\n.navbar-nav > li {\r\n  display: inline-flex;\r\n  padding: 5px;\r\n  margin-left: 2px;\r\n  color: #fff;\r\n  cursor: pointer;\r\n  transition: 0.3s linear all;\r\n  user-select: none;\r\n}\r\n.navbar-nav > li:hover {\r\n  color: #faa;\r\n  box-shadow: 0 80px 5px rgba(255, 255, 255, 0.15) inset;\r\n}\r\n.navbar-nav .nav-item .nav-link:focus-within {\r\n  color: #dfff00;\r\n  font-weight: bold;\r\n}\r\n\r\n/* ########## responsive ########## */\r\n/* Extra small devices (phones, 600px and down) */\r\n@media only screen and (max-width: 600px) {\r\n  .main-navbar {\r\n    font-family: \"Arimo\", sans-serif;\r\n    background: linear-gradient(135deg, #fe2929, #ffb3c5);\r\n    background-color: #184e8e;\r\n    position: fixed;\r\n    z-index: 1020;\r\n    width: 100%;\r\n  }\r\n  .navbar-toggler {\r\n    float: right;\r\n    margin-right: 10px;\r\n    margin-top: 10px;\r\n    margin-bottom: 10px;\r\n  }\r\n  nav .navbar-collapse ul {\r\n    padding: 25px 15px;\r\n    margin: 0;\r\n    padding: 0;\r\n    width: 100%;\r\n    display: inline-block;\r\n    /* list-style: none; */\r\n    /* float: right; */\r\n  }\r\n}\r\n\r\n/* Small devices (portrait tablets and large phones, 600px and up) */\r\n@media only screen and (min-width: 600px) {\r\n}\r\n\r\n/* Medium devices (landscape tablets, 768px and up) */\r\n@media only screen and (min-width: 768px) {\r\n}\r\n\r\n/* Large devices (laptops/desktops, 992px and up) */\r\n@media only screen and (min-width: 992px) {\r\n}\r\n\r\n/* Extra large devices (large laptops and desktops, 1200px and up) */\r\n@media only screen and (min-width: 1200px) {\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
